/* eslint-disable */
import { getPostList } from '@/api/post'
import { RESPONSE_CODE } from '@/api/config'
export default {
    data () {
        return {
            pullup: true,
            postList: [],
            loading: true,
            tip: '...'
        }
    },
    created(){
        this._initPostList()
    },
    methods: {
        doCopy(value) {
            this.$copyText(value).then(e=> {
                this.$vux.toast.text('', 'middle')
            })
        },
        _initPostList(){
            this.page = 0
            this.size = 6
            getPostList(this.page, this.size).then(res=>{
                if( res.code === RESPONSE_CODE) {
                    this.postList = res.data.list
                    this.loading = res.data.hasNext
                    this.tip = this.loading ? '...':'~'
                }
            })
        },
        nextPostList() {
            if(!this.loading){
                return
            }
            getPostList(this.page++, this.size++).then(res=>{
                if( res.code === RESPONSE_CODE) {
                    this.postList = this.postList.concat(res.data.list)
                    this.loading = res.data.hasNext
                    this.tip = this.loading ? '...':'~'
                }
            })
        },
        upDown(){
            
            this.nextPostList()
        },
        
    }
}



